import { mapState, mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: '',
      obj: '',
      currentIndex: 0
    }
  },
  onLoad (options) {
    this.oJson = JSON.parse(decodeURIComponent(options.oJson))
    this.getConsultInfoFn()
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.getConsultInfoFn()
  },
  computed: {
    ...mapState([
      'isIos',
      'isReview',
      'userInfo'
    ])
  },
  methods: {
    ...mapMutations({
      setIsIos: 'SET_IS_IOS'
    }),
    ...mapActions([
      'getConsultInfo'
    ]),
    // 获取咨询信息
    getConsultInfoFn () {
      uni.showLoading({ title: 'Loading' })
      this.getConsultInfo({ that: this, id: this.oJson.id }).then((res) => {
        this.obj = res
        uni.hideLoading()
        uni.stopPullDownRefresh()
      })
    },
    onSwitchNavFn (index) {
      this.currentIndex = index
    },
    // 跳转页面
    onSkipPageFn (url) {
      if ((this.userInfo && this.isIos && this.isReview) || (this.userInfo && !this.isIos)) {
        uni.navigateTo({
          url: url + encodeURIComponent(JSON.stringify(this.oJson))
        })
      } else if ((this.isIos && this.isReview) || (!this.isIos)) {
        uni.navigateTo({
          url: `/pages/login/index`
        })
      }
    }
  },
  onUnload () {
    this.obj = ''
  }
}
